import {
	JSON as xsJSON
} from "../polyfill/index.js";

import utils from "../util/index.js";

if(!String.prototype.trim) {
	String.prototype.trim = function() {
		return this.replace(/^\s\s*/, '').replace(/\s\s*$/, '');
	}
}

let idCount = 2019;
//生成一个随机的id，只保证在本页面是唯一的
function randId(suffix) {
	let id = "r" + parseInt(new Date().getTime() / 1000) + "_" + parseInt(Math.random() * 1000) + "_" + (idCount++);
	if(suffix !== undefined) {
		id += suffix;
	}
	return id;
}

function xsEval(scriptString) {
	try {
		let rs = xsloader.IE_VERSION > 0 && xsloader.IE_VERSION < 9 ? eval("[" + scriptString + "][0]") : eval("(" + scriptString + ")");
		return rs;
	} catch(e) {
		throw e;
	}
}

/**
 * 注意key需要用引号包裹
 * @param {Object} str
 * @param {Object} option
 */
function xsParseJson(str, option) {
	if(str === "" || str === null || str === undefined || !xsloader.isString(str)) {
		return str;
	}

	option = xsloader.extend({
		fnStart: "", //"/*{f}*/",
		fnEnd: "", //"/*{f}*/",
		rcomment: "\\/\\/#\\/\\/"
	}, option);

	let fnMap = {};
	let fnOffset = 0;
	let replacer = undefined;
	if(option.fnStart && option.fnEnd) {
		while(true) {
			let indexStart = str.indexOf(option.fnStart, fnOffset);
			let indexEnd = str.indexOf(option.fnEnd, indexStart == -1 ? fnOffset : indexStart + option.fnStart.length);
			if(indexStart == -1 && indexEnd == -1) {
				break;
			} else if(indexStart == -1) {
				console.warn("found end:" + option.fnEnd + ",may lack start:" + option.fnStart);
				break;
			} else if(indexEnd == -1) {
				console.warn("found start:" + option.fnStart + ",may lack end:" + option.fnEnd);
				break;
			}
			let fnId = "_[_" + randId() + "_]_";
			let fnStr = str.substring(indexStart + option.fnStart.length, indexEnd).trim();
			if(!xsloader.startsWith(fnStr, "function(")) {
				throw "not a function:" + fnStr;
			}
			try {
				str = str.substring(0, indexStart) + '"' + fnId + '"' + str.substring(indexEnd + option.fnEnd.length);
				let fn = xsloader.xsEval(fnStr);
				fnMap[fnId] = fn;
			} catch(e) {
				console.error(fnStr);
				throw e;
			}
			fnOffset = indexStart + fnId.length;
		}
		replacer = function(key, val) {
			if(xsloader.isString(val) && fnMap[val]) {
				return fnMap[val];
			} else {
				return val;
			}
		};
	}

	if(option.rcomment) {
		str = str.replace(/(\r\n)|\r/g, "\n"); //统一换行符号
		str = str.replace(new RegExp(option.rcomment + "[^\\n]*(\\n|$)", "g"), ""); //去除行注释
	}

	try {
		let jsonObj = xsJSON;
		return jsonObj.parse(str, replacer);
	} catch(e) {
		try {
			let reg = new RegExp('position[\\s]*([0-9]+)[\\s]*$')
			if(e.message && reg.test(e.message)) {
				let posStr = e.message.substring(e.message.lastIndexOf("position") + 8);
				let pos = parseInt(posStr.trim());
				let _str = str.substring(pos);
				console.error(e.message + ":" + _str.substring(0, _str.length > 100 ? 100 : _str.length));
			}
		} catch(e) {}
		throw e;
	}
}

function xsJson2String(obj) {
	let jsonObj = xsJSON;
	return jsonObj.stringify(obj);
}

function getPathWithRelative(path, relative, isPathDir) {

	let relativeQuery = "";
	let qIndex = path.lastIndexOf("?");
	if(qIndex >= 0) {
		path = path.substring(0, qIndex);
	} else {
		qIndex = path.lastIndexOf("#");
		if(qIndex >= 0) {
			path = path.substring(0, qIndex);
		}
	}

	qIndex = relative.lastIndexOf("?");
	if(qIndex >= 0) {
		relativeQuery = relative.substring(qIndex);
		relative = relative.substring(0, qIndex);
	} else {
		qIndex = relative.lastIndexOf("#");
		if(qIndex >= 0) {
			relativeQuery = relative.substring(qIndex);
			relative = relative.substring(0, qIndex);
		}
	}

	let absolute = utils.dealPathMayAbsolute(relative);
	if(absolute.absolute) {
		return absolute.path + relativeQuery;
	}

	if(isPathDir === undefined) {
		let index = path.lastIndexOf("/");
		if(index == path.length - 1) {
			isPathDir = true;
		} else {
			if(index == -1) {
				index = 0;
			} else {
				index++;
			}
			isPathDir = path.indexOf(".", index) == -1;
		}
	}

	if(xsloader.endsWith(path, "/")) {
		path = path.substring(0, path.length - 1);
	}

	let isRelativeDir = false;
	if(relative == "." || xsloader.endsWith(relative, "/")) {
		relative = relative.substring(0, relative.length - 1);
		isRelativeDir = true;
	} else if(relative == "." || relative == ".." || xsloader.endsWith("/.") || xsloader.endsWith("/..")) {
		isRelativeDir = true;
	}

	let prefix = "";
	let index = -1;
	let absolute2 = utils.dealPathMayAbsolute(path);
	if(absolute2.absolute) {
		path = absolute2.path;
		let index2 = path.indexOf("//");
		index = path.indexOf("/", index2 + 2);
		if(index == -1) {
			index = path.length;
		}
	}

	prefix = path.substring(0, index + 1);
	path = path.substring(index + 1);

	let stack = path.split("/");
	if(!isPathDir && stack.length > 0) {
		stack.pop();
	}
	let relatives = relative.split("/");
	for(let i = 0; i < relatives.length; i++) {
		let str = relatives[i];
		if(".." == str) {
			if(stack.length == 0) {
				throw new Error("no more upper path!");
			}
			stack.pop();
		} else if("." != str) {
			stack.push(str);
		}
	}
	if(stack.length == 0) {
		return "";
	}
	let result = prefix + stack.join("/");
	if(isRelativeDir && !xsloader.endsWith(result, "/")) {
		result += "/";
	}
	result = appendArgs2Url(result, relativeQuery);
	return result;
}

function _toParamsMap(argsStr, decode = true) {
	if(isObject(argsStr)) {
		return argsStr;
	}
	if(!argsStr) {
		argsStr = location.search;
	}

	let index = argsStr.indexOf("?");
	if(index >= 0) {
		argsStr = argsStr.substring(index + 1);
	} else {
		if(utils.dealPathMayAbsolute(argsStr).absolute) {
			return {};
		}
	}
	index = argsStr.lastIndexOf("#");
	if(index >= 0) {
		argsStr = argsStr.substring(0, index);
	}

	let ret = {},
		seg = argsStr.split('&'),
		s;
	for(let i = 0; i < seg.length; i++) {
		if(!seg[i]) {
			continue;
		}
		s = seg[i].split('=');
		let name = decode ? decodeURIComponent(s[0]) : s[0];
		ret[name] = decode ? decodeURIComponent(s[1]) : s[1];
	}
	return ret;
}

function appendArgs2Url(url, urlArgs) {
	if(url === undefined || url === null || !urlArgs) {
		return url;
	}

	function replaceUrlParams(myUrl, newParams) {
		for(let x in newParams) {
			let hasInMyUrlParams = false;
			for(let y in myUrl.params) {
				if(x.toLowerCase() == y.toLowerCase()) {
					myUrl.params[y] = newParams[x];
					hasInMyUrlParams = true;
					break;
				}
			}
			//原来没有的参数则追加
			if(!hasInMyUrlParams) {
				myUrl.params[x] = newParams[x];
			}
		}
		let _result = myUrl.protocol + "://" + myUrl.host + (myUrl.port ? ":" + myUrl.port : "") + myUrl.path + "?";

		for(let p in myUrl.params) {
			_result += (p + "=" + myUrl.params[p] + "&");
		}

		if(_result.substring(_result.length - 1) == "&") {
			_result = _result.substring(0, _result.length - 1);
		}

		if(myUrl.hash != "") {
			_result += "#" + myUrl.hash;
		}
		return _result;
	}

	let index = url.lastIndexOf("?");
	let hashIndex = url.lastIndexOf("#");
	if(hashIndex < 0) {
		hashIndex = url.length;
	}
	let oldParams = index < 0 ? {} : _toParamsMap(url.substring(index + 1, hashIndex), false);
	let newParams = _toParamsMap(urlArgs, false);
	let has = false;
	for(let k in newParams) {
		if(oldParams[k] != newParams[k]) {
			oldParams[k] = newParams[k];
			has = true;
		}
	}
	if(!has) {
		return url; //参数没有变化直接返回
	}

	let paramKeys = [];
	for(let k in oldParams) {
		paramKeys.push(k);
	}
	paramKeys.sort();

	let path = index < 0 ? url.substring(0, hashIndex) : url.substring(0, index);
	let params = [];

	for(let i = 0; i < paramKeys.length; i++) { //保证参数按照顺序
		let k = paramKeys[i];
		params.push(k + "=" + oldParams[k]);
	}
	params = params.join("&");
	let hash = "";
	if(hashIndex >= 0 && hashIndex < url.length) {
		hash = url.substring(hashIndex);
	}
	return path + (params ? "?" + params : "") + (hash ? hash : "");

}

function queryString2ParamsMap(argsStr, decode) {
	return _toParamsMap(argsStr, decode);
}

export default {
	randId,
	xsEval,
	xsParseJson,
	xsJson2String,
	getPathWithRelative,
	appendArgs2Url,
	queryString2ParamsMap
};